import Vue from 'vue'
import VueRouter from 'vue-router'
import { LoadingBar } from 'view-design'

import Main from '@/components/layout/Index'

Vue.use(VueRouter)

//系统页面定义
const CONST_ROUTER = [
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '登录'
    },
    component: () => import('@/views/Login.vue')
  },
  {
    path: '/401',
    name: '401',
    meta: {
      title: '登录失效'
    },
    component: () => import('@/views/401.vue')
  },
  {
    path: '/404',
    name: '404',
    meta: {
      title: '未找到页面'
    },
    component: () => import('@/views/404.vue')
  },
  {
    path: '*',
    redirect: { name: '404' }
  },
  {
    path: '/500',
    name: '500',
    meta: {
      title: '未找到页面'
    },
    component: () => import('@/views/500.vue')
  }
]

//动态路由定义 后续可能会接入动态权限
const routes = [
  {
    path: '/',
    name: 'main',
    component: Main,
    children: [
      {
        path: 'user',
        name: 'user',
        meta: {
          title: '用户列表'
        },
        component: () => import('@/views/user/List')
      },
      {
        path: 'user/edit',
        name: 'user-edit',
        meta: {
          title: '编辑用户'
        },
        component: () => import('@/views/user/Edit')
      },
      {
        path: 'user/show',
        name: 'user-show',
        meta: {
          title: '用户详情'
        },
        component: () => import('@/views/user/Show')
      },
      {
        path: 'menu',
        name: 'menu',
        meta: {
          title: '菜单列表'
        },
        component: () => import('@/views/menu/List')
      },
      {
        path: 'menuAddModule',
        name: 'menuAddModule',
        component: () => import('@/views/menu/AddModule')
      },
      {
        path: 'menuAddMenu',
        name: 'menuAddMenu',
        component: () => import('@/views/menu/AddMenu')
      },
      {
        path: 'application',
        name: 'application',
        component: () => import('@/views/application/List')
      },
      {
        path: 'application/add',
        name: 'application-add',
        component: () => import('@/views/application/Add')
      },
      {
        path: 'application/edit',
        name: 'application-edit',
        component: () => import('@/views/application/Edit')
      },
      {
        path: 'article',
        name: 'article',
        component: () => import('@/views/article/List')
      },
      {
        path: 'artilce/add',
        name: 'article-add',
        component: () => import('@/views/article/Add')
      },
      {
        path: 'article/edit',
        name: 'article-edit',
        component: () => import('@/views/article/Edit')
      },
      {
        path: 'column/list',
        name: 'column-list',
        component: () => import('@/views/article/ColumnList')
      },
      {
        path: 'column/add',
        name: 'column-add',
        component: () => import('@/views/article/ColumnAdd')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  routes: [...CONST_ROUTER, ...routes]
})

router.beforeEach((to, from, next) => {
  window.document.title = to.meta.title || '边互通后台管理系统'
  LoadingBar.start()
  next()
})

router.afterEach(() => {
  LoadingBar.finish()
})

export default router
