import { REGION } from '@/data/region.js'

// 防抖
export const debounce = function(func, wait = 150) {
  let timer = 0
  let self = this
  return function(...args) {
    if (timer) {
      // 如果在指定时间间隔内又再次触发，则继续延时
      clearTimeout(timer)
    }
    // 重点：使用$nextTick函数
    self.$nextTick(() => {
      timer = setTimeout(() => {
        func.apply(self, args)
      }, wait)
    })
  }
}

export const resize = function() {
  return debounce.call(this, function() {
    //
    let fixedHeight = 204
    if (this.$refs.toolsBar) {
      fixedHeight += 14
    }

    //如果屏幕宽度小于1440 表格样式使用small
    if (window.innerWidth > 1440) {
      this.tableOpts.size = 'large'
    }

    this.tableOpts.height =
      window.innerHeight -
      (this.$refs.searchDiv ? this.$refs.searchDiv.$el.offsetHeight : 0) -
      fixedHeight -
      (this.$refs.toolsBar ? this.$refs.toolsBar.$el.offsetHeight : 0)
  })
}

// 时间格式化
export function formatDate(date, fmt) {
  let o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 小时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    S: date.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
      )
    }
  }
  return fmt
}

// 时间格式化2018-08-07T00:00:00
export function formatDateT(date) {
  let str = ''
  if (date) {
    let arr = date.split('T')
    str = arr[0] + ' ' + arr[1]
  }
  return str
}

//返回口岸信息
export const getRegionName = row => {
  //需要做优化
  if (row.customs === null) {
    return
  }
  let customs = REGION.find(item => {
    return item.value === row.customs
  })
  // console.log(customs)
  let region = customs.children.find(item => {
    return item.value === row.region
  })
  return customs.label + ' / ' + region.label
}

// 菜单数据处理(1给一级二级三级菜单加上标识 2 给子菜单加上父极id)
export function menu(date) {
  let parentid = ''
  let moduleid = ''
  let parenttitle = ''
  let moduletitle = ''
  date.forEach(item1 => {
    item1.type = 'parent'
    item1.title = item1.name
    parentid = item1.id
    parenttitle = item1.name
    if (item1.children.length > 0) {
      item1.children.forEach(item2 => {
        item2.type = 'module'
        moduleid = item2.id
        moduletitle = item2.name
        item2.parentid = parentid
        item2.title = parenttitle
        if (item2.children.length > 0) {
          item2.children.forEach(item3 => {
            item3.type = 'menu'
            item3.parentid = moduleid
            item3.title = moduletitle
          })
        }
      })
    }
  })
  //console.log('菜单', date)
  return date
}
